body {
    margin: 0;
    font-size: 28px;
    background: cornflowerblue;
}

.header {
    background-color: #f1f1f1;
    padding: 30px;
    text-align: center;
}
.matrix{
    width: auto;
    text-align: center;
    border-radius: 25px;
    border-right: red solid;
    border-left: red solid;
    padding: 10px;
    margin: auto;
    overflow: scroll visible;
    min-width: 120px;

}
#navbar {
    overflow: hidden;
    background-color: #333;

}

#navbar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

#navbar a:hover {
    background-color: #ddd;
    color: black;
}

#navbar a.active {
    background-color: #4CAF50;
    color: white;
}

.content {
    padding: 16px;
}

.sticky {
    position: fixed;
    top: -1px;
    width: 100%;
    z-index: 1;
}

.sticky + .content {
    padding-top: 92px;
}